<template>
  <div>
    <top></top>
    <div class="delivery">
      <div style="border-bottom: 1px solid #E2E4E7;padding-bottom: 0.25rem">
        <h2>运费</h2>
        <p style="font-size: 0.14rem;color: #666666;">向每件商品单独征收运费，并根据买家增加商品而追加运费</p>
        <div style="display: flex;margin-top: 0.2rem;">
          <div>
            <h3>首件运费</h3>
            <input type="text" v-model="pay" class="input" @focus="textF"  @blur="input">
          </div>
          <div style="margin-left: 0.15rem">
            <h3>每添加一件费用</h3>
            <input type="text" v-model="onePay" class="input" @focus="textF"  @blur="input">
          </div>
        </div>
      </div>
      <div style="padding:0.2rem 0;border-bottom: 1px solid #E2E4E7">
        <h2 style="margin-bottom: 0.1rem">预定</h2>
        <div style="display: flex;align-items: center;margin-bottom: 0.1rem">
          <input type="checkbox" v-model="select">
          <span style="margin-left: 0.1rem;font-size: 0.14rem;display: inline-block">将我的商品显示为需要预订并设置预计发货时间</span>
        </div>
        <div style="display: flex;flex-direction: row;align-items: center" v-if="select">
          <h3 style="margin-right:0.15rem">预计发货日期</h3>
          <DatePicker type="date" :options="options" placeholder="选择日期" style="width: 200px"></DatePicker>
        </div>
      </div>
      <div style="margin: 0.2rem 0">
        <h2 class="delivery_h2">预计发货日期</h2>
        <p>买家在结账时将能看到你的留言（选填）。</p>
        <textarea  cols="30" rows="10" class="text" placeholder="例如 : 快递信息、预计发货或到达日期等" @focus="textF"  @blur="input"></textarea>
      </div>
      <a href="javascript:void(0)" class="a">保存</a>
    </div>
  </div>
</template>
<script>
  import top from '../../top.vue'
  export default{
    data(){
      return{
        pay:'0.00',
        onePay:'0.00',
        select:false,
        options: {
          disabledDate (date) {
            return date && date.valueOf() < Date.now() - 86400000;
          }
        },
      }
    },
    methods:{
      textF(){
        event.target.style.borderColor='skyblue';
      },
      input(){
        event.target.style.borderColor='gainsboro';
      }
    },
    components:{
      top
    },
    mounted(){

    }
  }
</script>
<style scoped>
  .delivery{
    padding: 0.15rem 0.15rem;
    margin-top: 0.2rem;
    background: white;
    box-shadow: 5px 5px 10px #E7E7E7;
    
  }
  h2{
    width: 0.6rem;
    text-align: center;
    color: #111111;
    font-size: 0.15rem;
    font-weight: 500;
    border-left:0.03rem solid #25B3EF;
    border-top-left-radius: 0.03rem;
    border-bottom-left-radius: 0.03rem;
  }
  h3{
    color: #111111;
    font-size: 0.14rem;
    font-weight: 500;
    padding-bottom: 0.05rem;
  }
  .input{
    display: inline-block;  width:3rem;  border-radius:0.05rem;  border: 1px solid gainsboro;  padding: 0.1rem 0.1rem;
  }
  .delivery p{
    padding: 0.05rem 0;
    font-size: 0.14rem;
  }
  .text{
    display: inline;
    width: 30vw;
    height: 1rem;
    padding: 0.1rem;
    border: 1px solid gainsboro;
    border-radius: 0.05rem;
  }
  .a{
    color: white;
    display: inline-block;
    padding: 0.1rem 0.3rem;
    border-radius: 0.05rem;
    background-color: #59A2ED;
  }
  .delivery_h2{
    width: 1.2rem;
    text-align: center;
    color: #111111;
    font-size: 0.15rem;
    font-weight: 500;
    border-left:0.03rem solid #25B3EF;
    border-top-left-radius: 0.03rem;
    border-bottom-left-radius: 0.03rem;
  }
</style>



